.Home {
  input {
    border-radius: 10px;
    height: 2em;
    padding: 6px 12px;
  }

  .submit {
    margin: 6px;
    border: 1px #CCC solid;
    border-radius: 3px;
    border-radius: 10px;
    padding: 5px;
    cursor: pointer;
  }

  .submit:hover {
    background-color: #999;
    color: #000;
  }

  .view {
    z-index: 100;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    border: none;
    background-color: #000;

    iframe {
      width: 100%;
      height: 100%;
    }
  }

  .viewHide {
    opacity: 0;
    position: fixed;
    top: -1000px;
    left: -1000px;
    width: 1px;
    height: 1px;
    border: none;

    iframe {
      width: 100%;
      height: 100%;
    }
  }
}